
import { defineComponent } from 'vue';

export default defineComponent({
  props: {
    active: {
      type: Boolean,
      default: false,
    },
    name: {
      type: String,
      required: true,
    },
    pinned: {
      type: Boolean,
      required: false,
      default: false,
    },
  },
  emits: ['close'],
  data() {
    return {
      show: false,
    };
  },
  computed: {
    closeButtonClasses() {
      const classes = 'align-self-start ml-2 mt-n4 mr-n3'.split(' ');
      if (this.pinned) classes.push('pinned');
      return classes;
    },
  },
  mounted() {
    this.show = true;
  },
  methods: {
    close() {
      if (!this.pinned && this.show) {
        this.show = false;
        setTimeout(() => this.$emit('close'), 100);
      }
    },
  },
});
