
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'VShield',
  props: {
    label: {
      type: String,
      required: true,
    },
    message: {
      type: String,
      required: true,
    },
  },
  computed: {
    escapedMessage(): string {
      return this.message.replaceAll('-', '--').replaceAll('_', '__');
    },
  },
});
